#About {
  padding: 100px 30px;
  background-color: #f1f1f1;
}

.About__wrapper {
  max-width: 1300px;
  margin: auto;
  padding: 0 40px;
}

.About__wrapper h1 {
  text-align: center;
  margin-bottom: 80px;
  position: relative;
  font-size: 26px;
  padding-bottom: 20px;
  color: inherit;
}

.About__wrapper h1::before {
  content: "";
  position: absolute;
  width: 80px;
  height: 3px;
  background-color: #111111;
  bottom: 0;
  left: calc(50% - 40px);
}

.About__wrapper h1::after {
  content: "";
  position: absolute;
  width: 16px;
  height: 10px;
  background-color: #111111;
  border: 4px solid #f1f1f1;
  left: calc(50% - 12px);
  bottom: -7px;
}

.About__content {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.About__content img {
  width: 200px;
  border-radius: 50%;
  margin-right: 100px;
  transition: 0.3s linear;
}

.About__text {
  flex: 1;
}

.About__text h3 {
  margin: 10px 0;
  color: #444444;
  font-size: 14px;
}

.About__text h3 span::before {
  content: "";
  width: 6px;
  height: 6px;
  background-color: #444444;
  display: inline-block;
  border-radius: 50%;
  margin: 3px 6px;
}

.About__text p {
  font-size: 17px;
  text-align: justify;
  line-height: 26px;
  margin-top: 20px;
}

.Skills {
  margin-top: 40px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.Skill {
  width: calc(50% - 20px);
  margin: 15px 0;
}

.Skill__info {
  display: flex;
  justify-content: space-between;
}

.skill-bar {
  height: 3px;
  background-color: #dddddd;
  margin-top: 14px;
  position: relative;
}

.skill-bar::after {
  content: "";
  position: absolute;
  height: 6px;
  background-color: #1f1f1f;
  bottom: 0;
  transition: 2s linear;
}

.React::after {
  width: 80%;
  animation: React 3s;
}

.JavaScript::after {
  width: 70%;
  animation: JavaScript 3s;
}

.HTML::after {
  width: 90%;
  animation: HTML 3s;
}

.CSS::after {
  width: 75%;
  animation: CSS 3s;
}

.React_Native::after {
  width: 65%;
  animation: CSS 3s;
}

@keyframes React {
  0% {
    width: 0%;
  }
  100% {
    width: 80%;
  }
}

@keyframes JavaScript {
  0% {
    width: 0%;
  }
  100% {
    width: 70%;
  }
}

@keyframes HTML {
  0% {
    width: 0%;
  }
  100% {
    width: 90%;
  }
}

@keyframes CSS {
  0% {
    width: 0%;
  }
  100% {
    width: 75%;
  }
}

@keyframes React_Native {
  0% {
    width: 0%;
  }
  100% {
    width: 65%;
  }
}

@media screen and (max-width: 900px) {
  .About__content img {
    margin: 0 auto 60px;
  }

  .About__text {
    flex: 100%;
    text-align: center;
  }
}

@media screen and (max-width: 600px) {
  .Skill {
    width: 100%;
  }

  .Contacts__wrapper h1 {
    font-size: 40px;
  }
}
