.Skill {
  width: calc(50% - 20px);
  margin: 15px 0;
}

.Skill__info {
  display: flex;
  justify-content: space-between;
}

.skill-bar {
  height: 3px;
  background-color: #dddddd;
  margin-top: 14px;
  position: relative;
}

.skill-bar::after {
  content: "";
  position: absolute;
  height: 6px;
  background-color: #1f1f1f;
  bottom: 0;
  transition: 2s linear;
}

.React::after {
  width: 80%;
  -webkit-animation: React 3s;
          animation: React 3s;
}

.JavaScript::after {
  width: 70%;
  -webkit-animation: JavaScript 3s;
          animation: JavaScript 3s;
}

.HTML::after {
  width: 90%;
  -webkit-animation: HTML 3s;
          animation: HTML 3s;
}

.CSS::after {
  width: 75%;
  -webkit-animation: CSS 3s;
          animation: CSS 3s;
}

.React_Native::after {
  width: 65%;
  -webkit-animation: CSS 3s;
          animation: CSS 3s;
}

@-webkit-keyframes React {
  0% {
    width: 0%;
  }
  100% {
    width: 80%;
  }
}

@keyframes React {
  0% {
    width: 0%;
  }
  100% {
    width: 80%;
  }
}

@-webkit-keyframes JavaScript {
  0% {
    width: 0%;
  }
  100% {
    width: 70%;
  }
}

@keyframes JavaScript {
  0% {
    width: 0%;
  }
  100% {
    width: 70%;
  }
}

@-webkit-keyframes HTML {
  0% {
    width: 0%;
  }
  100% {
    width: 90%;
  }
}

@keyframes HTML {
  0% {
    width: 0%;
  }
  100% {
    width: 90%;
  }
}

@-webkit-keyframes CSS {
  0% {
    width: 0%;
  }
  100% {
    width: 75%;
  }
}

@keyframes CSS {
  0% {
    width: 0%;
  }
  100% {
    width: 75%;
  }
}

@-webkit-keyframes React_Native {
  0% {
    width: 0%;
  }
  100% {
    width: 65%;
  }
}

@keyframes React_Native {
  0% {
    width: 0%;
  }
  100% {
    width: 65%;
  }
}

